import { createAsyncThunk } from '@reduxjs/toolkit';
import { api, store } from '.';
import { APIRoute } from '../const';
import { errorHandle } from '../services/error-handle';
import { Comments } from '../types/comments';
import { Guitar, Guitars } from '../types/guitar';
import { loadComments, toggleLoaderComments } from './comments-data/comments-data';
import { loadGuitarFull, toggleLoaderGuitarFull } from './guitar-full-data/guitar-full-data';
import { loadGuitars } from './guitars-data/guitars-data';


export const fetchGuitarsAction = createAsyncThunk(
  'data/fetchGuitars',
  async () => {
    try {
      const {data} = await api.get<Guitars>(APIRoute.Guitars);
      store.dispatch(loadGuitars(data));
    } catch(error) {
      errorHandle(error);
    }
  },
);

export const fetchFullGuitarAction = createAsyncThunk(
  'data/fetchFullGuitar',
  async (id: number) => {
    try {
      const {data} = await api.get<Guitar>(`${APIRoute.Guitars}/${id}`);
      store.dispatch(loadGuitarFull(data));
    } catch (error) {
      errorHandle(error);
      store.dispatch(toggleLoaderGuitarFull());
    }
  },
);

export const fetchCommentsAction = createAsyncThunk(
  'data/fetchComments',
  async (id: number | null) => {
    try {
      const {data} = await api.get<Comments>(`${APIRoute.Guitars}/${id}/comments`);
      store.dispatch(loadComments(data));
    } catch (error) {
      errorHandle (error);
      store.dispatch(toggleLoaderComments(true));
    }
  },
);
